import Vue from 'vue';
import App from './App.vue';
import VueModel, { Store } from '@/utils/vue-apollo-model';
import createApolloClient from './createApolloClient';

Vue.config.productionTip = false;

const graphqlClient = createApolloClient();
const store = new Store(graphqlClient);

Vue.use(VueModel, {
  store,
});

new Vue({
  render: (h) => h(App),
}).$mount('#app');
