
import { BaseModel, apolloQuery } from '@/utils/vue-apollo-model';
import gql from 'graphql-tag';

export default class HelloModel extends BaseModel {
    @apolloQuery({
        query: gql`
            query helloQuery {
                hello {
                    email
                }
            }
        `,
        initState: {
            hello: [],
        },
        prefetch: true,
    })
    public helloQuery!: any;
    public async add(email: string) {
        return this.$client.mutate({
            mutation: gql`
                mutation($email: String) {
                    add(info: { email: $email }) {
                        email
                    }
                }
            `,
            variables: {
                email,
            },
            updateQueries: {
                helloQuery: (prev, { mutationResult }) => {
                    const newItem = mutationResult.data.add;
                    return {
                        hello: prev.hello.concat(newItem),
                    };
                },
            },
        });
    }
}
