










import { Component, Prop, Vue } from 'vue-property-decorator';
import { Models } from '@/utils/vue-decorators';
import helloModel from './model';

@Component
export default class HelloWorld extends Vue {
  @Models(helloModel)
  private helloModel!: helloModel;
  get list() {
    return this.helloModel.helloQuery.hello;
  }
  private email = '';
  submit() {
    this.helloModel.add(this.email);
  }
}
